
body{
  font-family: 'Lato';
}
button{
  border: 0;
}

button:active, button:focus, .currency:active, .currency:focus{
  outline: none;
}

ul{
  margin: 0;
  padding: 0;
}

li{
  list-style-type: none;
}

a{
  text-decoration: none;
  color: inherit;
}

img{
  max-width: 100%;
}

.wrapper{
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 70px;
}

.icon{
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  flex-shrink: 0;
}

.h2{
  font-size: 30px;
  width: 20%px;
  text-align: center;
}

.section_name{
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: PlayfairDisplay;
  font-weight: 700;
  font-style: italic;
  line-height: 17.76px;
}

.line{
  height:2px;
  width: 30%;
  background-color: #eaeaea;
}

.rhombus{
  min-height:15px;
  min-width: 15px;
  max-height:15px;
  max-width: 15px;
  margin: 20px;
  background-color: #d9bf8f;
  transform: rotate(45deg);
}

.category, .gallery, .brand, .advantages{
  margin: 60px 0 80px 0;
}

.header_top_bar{
  background-color: #f2f2f2;
}

.header_top_bar .wrapper{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  color: #393939;
}

.header_logo .wrapper{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header_login{
  margin: 10px 0;
}
.header_login ul {
  display: flex;
  justify-content: flex-end;
}

.header_login li{
  padding-left: 30px;
  border-left: 1px solid #393939;
  font-size: 11px;
  margin-right: 30px;
}

.header_login li:hover, .header_nav a:hover{
  color: #feab12;
}

.header_login li:first-child{
  border: none;
}

.header_login li:last-child{
  margin-right: 0px;
}

.a_phone{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 11px;
  min-width: 300px;
  margin: 10px 0;
}

.a_phone:hover{
  color: #feab12;
}

.a_phone .icon{
  margin-right: 15px;
}

.header_logo{
  padding: 22px 0;
  position: relative;
}

.header_logo:after{
  z-index: 10;
  content: '';
  position: absolute;
  bottom: calc(0% - 64px);
  left: calc(50% - 200px);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 65px 200px 0 200px;
  border-color: #fff transparent transparent transparent;
}
.header_nav{
  font-family: PlayfairDisplay;
  font-size: 18px;
  font-style: italic;
}
.header_nav ul{
  display: flex;
}

.header_nav li{
  margin-right: 20px;
}

.header_nav a{
  display: block;
  width: 100%;
  height: 100%;
  padding-right: 2px;
}

.logo{
  font-size: 72px;
  font-family: "KrinkesDecorPERSONAL";
}

.icon_burger{
  margin-bottom: 10px;
  width: 40px;
  height: 40px;
  display: none;
  order: 0;
}
.logo_social_icon{
  display: flex;
  align-items: center;
}

.logo_social_icon_circle{
  width: 30px;
  height: 30px;
  background: white;
  margin-right: 15px;
  border: 2px solid #d9bf8f;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo_social_icon_circle:hover{
  border:  2px solid black;
}

.logo_social_icon_circle:hover .icon_header{
  fill: black;
}

.logo_social_icon form{
  margin-left: 40px;
}

.currency{
  background: black;
  color: white;
  font-size: 12px;
  padding: 10px 30px 10px 15px;
  -webkit-appearance: none;
  background: url("../images/arrow_to_down.png");
  background-position: 95% 50%;
}

.icon_header{
  fill: #d9bf8f;
}

.header_slider{
  position: relative;
}

.slider_img img{
  display: block;
  width: 100%;
}

.slider_info{
  position: absolute;
  top: 20%;
  min-width: 20%;
  width:20%;
  left: calc((100% - 20%)/2);
  text-align: center;
  color: white;
}

.slider_text{
  padding: 20px;
  border: 1px solid #ffffff;
  outline: 1px solid #ffffff;
  outline-offset: -10px;
}

.black_text{
  color: black;
}

.slider_name1{
  font-size: 19px;
  margin: 20px 0;
}
.slider_name2{
  font-size: 59px;
  margin: 25px 0;
}

.slider_name3{
  font-size: 31px;
}

.shop_now_slider{
  background: #feab12;
  color: white;
  font-size: 24px;
  padding: 15px 20px;
  margin: 50px 0;
  cursor: pointer;
  min-width: 75%;
}

.before_lines{
  position: absolute;
  width: 20%;
  height: 7px;
  bottom: 55%;
  left: 15%;
  border-bottom: 1px solid #fff;
}

.before_lines:before{
  content:'';
  position: absolute;
  width: 50%;
  height: 1px;
  top: 0%;
  left: 49%;
  border-bottom: 1px solid #fff;
}

.after_lines{
  position: absolute;
  width: 20%;
  height: 7px;
  bottom: 55%;
  left: 65%;
  border-bottom: 1px solid #fff;
}
.after_lines:before{
  content:'';
  position: absolute;
  width: 50%;
  height: 1px;
  top: 0%;
  left: 0%;
  border-bottom: 1px solid #fff;
}

.advantages .wrapper{
  display: flex;
  justify-content: space-between;
  color: white;
}

.advantages_items {
  background: #d9bf8f;
  padding: 20px 20px 20px 0;
  font-size: 14px;
  width: 30%;
  position: relative;
  display: flex;
  height: 60px;
}

.advantages_items svg{
  width: 48px;
  height: 49px;
  margin: 0px 10px;
}

.advantages_items:first-child svg {
  transform: rotate(-45deg);
}

.advantages_items:first-child:after {
  content: '';
  position: absolute;
  top: 0;
  right: -39px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 50px 0 50px 40px;
  border-color: transparent transparent transparent #d9bf8f;
  z-index: 5;
}

.advantages_items:last-child:before {
  content: '';
  position: absolute;
  top: 0;
  left: -39px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 50px 40px 50px 0;
  border-color: transparent #d9bf8f transparent transparent;
}

.advantages_centre:before {
  content: '';
  position: absolute;
  top: 0;
  left: -1px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 50px 0 50px 40px;
  border-color: transparent transparent transparent #ffffff;
}

.advantages_centre:after{
  content: '';
  position: absolute;
  top: 0;
  right: -2px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 50px 40px 50px 0;
  border-color: transparent #ffffff transparent transparent;
}

.advantages_name{
  font-size: 30px;
  margin-bottom: 10px;
}

.advantages_centre{
  background: black;
  padding: 20px 50px;
  margin: 0 35px;
}
.category{
  display: flex;
  justify-content: space-between;
  margin:60px -10px;
}

.category_column{
  display: flex;
  flex-wrap: wrap;
  flex-flow: column;
  justify-content: space-between;
  margin: 10px;
}

.category_column img{
  display: block;
  width: 400px;
  height: 400px;
  -o-object-fit: cover;
  object-fit: cover;
}

.category_column2 img{
  width: 460px;
  height: 810px;
}

.category_item{
  position: relative;
}

.handpicked2{
  position: absolute;
  top: 15%;
  left: 50%;
  margin-left: -83px;
  text-align: center;
  font-size: 14px;
}

.handpicked2_discount{
  font-size: 24px;
  margin-bottom: 12px;
}

.handpicked3{
  position: absolute;
  text-align: left;
  font-size: 36px;
  color: white;
  font-style: normal;
  top: 36%;
  left: 12%;
}
.handpicked3_collection{
  color: black;
  font-size: 24px;
  font-family: PlayfairDisplay;
  font-style: italic;
  margin-bottom: 15px;
}

.handpicked4 {
  position: absolute;
  font-size: 60px;
  top: 34%;
  left: 37%;
  margin-left: -8px;
  line-height: 48px;
  text-align: center;
}

.handpicked4_discount{
  font-size: 48px;
}

.shop_now {
  display: none;
  padding: 15px;
  font-size: 14px;
  color: white;
  background: #feab12;
  cursor: pointer;
  position: absolute;
  bottom: 60px;
  min-width: 40%;
  left: 30%;
}

.shop_now:hover, .shop_now_slider:hover{
  color: #feab12;
  background: #000000;
}

.hover_item {
  position: absolute;
  width: calc(100% - 40px);
  height: calc(100% - 40px);
  bottom: 20px;
  left: 20px;
  border:3px solid #ffffff;
  box-sizing: border-box;
  transition: 0.5s;
}

.cross {
  background: #feab12;
  height: 82px;
  width: 2px;
  position: relative;
  left: 50%;
  bottom: -30%;
  display: none;
}
.cross:after {
  content: "";
  height: 2px;
  width: 82px;
  background: #feab12;
  position: absolute;
  left: -40px;
  top: 40px;
}

.hover_item:hover{
  background:#656469;
  border: 2px solid transparent;
  background-color: rgba(0,0,0,0.3);
}

.hover_item:hover .shop_now{
  display: block;
}

.hover_item:hover .cross{
  display: block;
}

.gallery{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border: 1px solid #eaeaea;
  padding: 15px;
}

.gallery_item{
  display: block;
  width: calc(100%/4 - 30px);
  margin: 15px;
  position: relative;
}
.gallery_item a{
  display: block;
  width: 100%;
  height: 100%;
}
.gallery_item img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.circle_sale20{
  content: "-20%";
}

.circle_sale40 {
  content: "-40%";
}

.circle_sale20, .circle_sale40{
  font-size: 23px;
  background: #feab12;
  color: #ffffff;
  border-radius: 50%;
  padding: 5px;
  margin: 10px;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
}

.item_filter:hover{
  filter: brightness(70%);
}

.add_to_cart {
  color: white;
  font-size: 14px;
  outline: none;
  border: none;
  cursor: grab;
  position: absolute;
  bottom: 0;
  width: 80%;
  left:10%;
  background: linear-gradient(135deg, transparent 33px, #000000 0) top left,
  linear-gradient(-135deg, transparent 33px, #000000 0) top right;
  background-size: 51% 100%;
  background-repeat: no-repeat;
  padding: 15px 33px;
}

.add_to_cart:hover{
  background: linear-gradient(135deg, transparent 33px, #feab12 0) top left,
  linear-gradient(-135deg, transparent 33px, #feab12 0) top right;
  background-size: 51% 100%;
  background-repeat: no-repeat;
  padding: 15px 33px;
}

.brand{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.brand a{
  margin: 40px;
}

.designer .wrapper{
  display: flex;
  justify-content: space-between;
  margin-top: 70px;
}


.designer{
  background-color: #d9bf8f;
  position: relative;
}

.designer:after{
  content: '';
  position: absolute;
  top: -2px;
  left: calc(50% - 185px);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 30px 185px 0 185px;
  border-color: #fff transparent transparent transparent;
}

.designer_info{
  display: flex;
  padding: 30px 0 50px 0;
}

.designer_about{
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
}

.designer_about_onright{
  text-align: end;
}
.designer_about p{
  color: #ffffff;
  font-family: PlayfairDisplay;
  font-size: 18px;
  font-style: italic;
  line-height: 30.16px;
  margin: 50px 0;
}

.designer_name{
  color: #000000;
  margin: 20px 0;
  font-family: PlayfairDisplay;
  font-size: 30px;
  font-weight: 700;
  font-style: italic;
  line-height: 17.99px;
}
.designer_work{
  color: #7f7e7e;
  font-size: 14px;
  font-weight: 700;
  line-height: 29.98px;
  text-transform: uppercase;
}

footer{
  background-color: black;
  color: #737373;
}

.footer_big{
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  border-bottom: 1px solid  #737373;
  padding-bottom: 80px;
  position: relative;
}

.footer_big:before{
  content: '';
  position: absolute;
  bottom: -11px;
  left: calc(50% - 13px);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 13px 0 13px;
  border-color: #737373 transparent transparent transparent;
}

.footer_h {
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  margin: 50px 0;
}

.footer_h span{
  font-style: italic;
}

.footer_info{
  margin-right: 115px;
}

.footer_info li{
  margin: 15px 0;
}

.footer-small{
  padding: 25px 0;
}

.footer-small .icon{
  padding: 25px 25px 25px 0;
  width: 2em;
  height: 2em;
}

.footer_social_icon{
  display: flex;
}

.social_icon_circle{
  width: 50px;
  height: 50px;
  background: white;
  margin-right: 20px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social_icon_circle:hover{
  background: black;
}

.social_icon_circle:hover .icon_footer{
  fill: white;
}

.icon_footer{
  fill: black;
  font-size: 25px;
}

.search input:focus::placeholder{
  opacity: 0;
  transition: opacity 0.3s ease;
}

.search{
  border:  0.5px solid #c3c3c3;
  background: black;
  outline: none;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  font-size: 12px;
}

.search input{
  background: black;
  border: none;
  outline: none;
  color:  #737373;
  padding: 0 20px;
  flex-basis: 70%;
}

.search button{
  background:  #323030;
  color: white;
  padding: 10px 20px;
}

.search button:hover{
  cursor: pointer;
  background: white;
  color: black;
}


@media screen and (max-width: 1350px){
  .designer_about p{
    margin: 0;
  }

  .advantages_name {
    font-size: 24px;
  }
  .brand a {
    margin: 10px 20px;
  }
}

@media screen and (max-width: 1283px) {
  .wrapper{
    padding: 0 20px;
  }
  .before_lines, .after_lines{
    bottom: 58%;
  }
}

@media screen and (max-width: 1240px) {
  .designer_about p{
    margin: 10px 0;
  }
}

@media screen and (max-width: 1180px) {
  .category_column img{
    width: 300px;
    height: 300px;
  }
  .category_column2 img {
    width: 300px;
    height: 610px;
  }

  .category{
    justify-content: center;
  }

  .designer_name {
    margin: 10px 0;
  }

  .slider_name2 {
    font-size: 40px;
    margin: 5px 0;
  }

  .before_lines, .after_lines{
    border: none;
  }
  .before_lines:before, .after_lines:before{
    display: none;
  }

  .shop_now_slider{
    margin: 30px 0;
    padding: 10px;
    font-size: 14px;

  }
  .handpicked4{
    font-size: 36px;
  }
}

@media screen and (max-width: 1090px) {
  .advantages_name {
    font-size: 20px;
  }
}


@media screen and (max-width: 1045px) {
  .gallery_item {
    width: calc(100% / 2 - 30px);
  }

  .category, .gallery, .brand, .advantages {
    margin: 10px 0 10px 0;
  }

  .designer .wrapper{
    margin-top: 0px;
  }
  .header_logo .wrapper{
    flex-flow: column;
  }

  .logo{
    order: 3;
    margin-top: 20px;
  }

  .header_nav{
    margin: 20px 0;
  }

  .advantages_items svg{
    display: none;
  }

  .advantages_items {
    padding: 20px 50px;
  }
}

@media screen and (max-width: 970px){
  .line{
    width: 20%;
  }

  .designer .wrapper{
    flex-wrap: wrap;
    justify-content: center;
  }

  .designer_info{
    padding: 0px;
    margin: 10px 0px;
  }

  .advantages_items:after, .advantages_items:before{
    display: none;
  }
  .advantages_centre{
    margin: 0;
  }

  .advantages_items{
    height: inherit;
    padding: 20px;
  }
  .slider_info {
    min-width: 30%;
    width: 30%;
    left: calc((100% - 30%) / 2);
    top: 16%;
  }

  .category{
    flex-wrap: wrap;
  }

  .category_column3{
    flex-flow: row;
    width: 620px;
  }
}

@media screen and (max-width: 880px) {
  .footer_info{
    margin-right: 30px;
  }

  .slider_name1{
    margin: 10px;
  }

  .header_logo:after{
    display: none;
  }
  .slider_info{
    top: 4%;
  }
}

@media screen and (max-width: 820px) {
  .advantages_items{
    justify-content: center;
  }
}

@media screen and (max-width: 690px) {
  .advantages_items{
    padding: 20px 10px;
  }
}

@media screen and (max-width: 680px){
  .category_column3{
    flex-flow: column;
    justify-content: center;
    align-items: center;
  }

  .category_item{
    margin-bottom: 10px;
  }
  .category_column2{
    margin: 0 10px;
  }

  .shop_now_slider{
    margin: 10px 0;
  }
}

@media screen and (max-width: 660px){
  .line, .designer:after{
    display: none;
  }
  .section_name{
    justify-content: center;
  }

  .slider_name2{
    font-size: 30px;
  }

  .slider_name3{
    font-size: 20px;
  }

  .slider_info{
    top: 5%;
  }
  .advantages_items{
    font-size: 14px;
    width: 100%;
    justify-content: left;
  }
  .advantages .wrapper{
    flex-wrap: wrap;
  }
  .advantages_items svg{
    display: block;
  }
}

@media screen and (max-width: 630px){
  .slider_name1{
    display: none;
  }

  .shop_now_slider{
    width: inherit;
  }

  .slider_info{
    top: 10%;
  }
}

@media screen and (max-width: 560px) {
  .gallery_item {
    width: calc(100% - 30px);
  }

  .line {
    width: 10%;
  }

  .footer_h {
    margin: 20px 0;
  }

  .footer_big {
    padding-bottom: 20px;
  }

  .header_login li {
    padding-left: 10px;
    margin-right: 10px;
  }

  .social_icon_circle {
    margin-right: 10px;
  }

  .slider_name2 {
    font-size: 14px;
  }

  .designer_name {
    font-size: 20px;
  }

  .search button {
    padding: 10px;
  }

  .social_icon_circle {
    width: 40px;
    height: 40px;
  }

  .logo_social_icon_circle {
    margin-right: 10px;
  }

  .shop_now_slider{
    min-width: 100%;
    margin: 10px 0;
    padding: 5px;

  }
  .slider_name3 {
    font-size: 12px;
  }

  .h2{
    font-size: 24px;
    line-height: 30px;
  }

  .logo_social_icon form{
    margin-left: 30px;
  }
  .icon_burger{
    display: block;
    align-self: flex-start;
  }

  .header_nav{
    display: none;
    position: absolute;
    z-index: 200;
    width: 80%;
    padding: 0;
    background: #f2f2f2;
    top: 0;
    right: 0;
    margin: 0;
    align-self: flex-end;

  }
  .header_nav ul{
    flex-flow: column;
  }
  .header_nav li{
    text-align: right;
    padding: 10px 20px;
    border-bottom: 1px solid #d9bf8f;
    text-align: left;
    margin: 0;
  }
}

@media screen and (max-width: 430px) {
  .slider_text {
    padding: 10px;
  }

  .handpicked3{
    font-size: 30px;
  }
}



.slider{
  overflow: hidden;
}
.slick-track{
  transform:none;
  width: 100%;
}
.slick-next, .slick-prev{
  display: none;
}
.slick-dots{
  bottom: 3%;
}
.slick-dots li button{
  display:none;
}

.slick-dots>li{
  width:13px;
  height:13px;
  transform: rotate(45deg);
  background: #eaeaea;
  margin: 5px;
}
.slick-dots li.slick-active{
  background: #000000;
}

#scroller{
  position: fixed;
  bottom: 12%;
  right: 5%;
